import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Material } from '../../../models/material';
import { MaterialService } from '../../../service/material.service';

@Component({
  selector: 'app-material-create',
  templateUrl: './material-create.component.html',
  styleUrls: ['./material-create.component.css']
})
export class MaterialCreateComponent implements OnInit {

  materialForm;

  constructor(public materialService: MaterialService, private formBuilder: FormBuilder, private router: Router) { 
    this.materialForm = this.formBuilder.group({
      name: ['', Validators.required],
      density: [''],
      user_id: ['']
    });
  }

  ngOnInit(): void {
  }

  createMaterial( materialForm ): void {
    this.materialForm.controls['user_id'].setValue(sessionStorage.getItem("user"));
    
    this.materialService.createMaterial(this.materialForm.value).pipe(first()).subscribe(       
      data => {
      this.router.navigate(['/materiallist']);
    },
    error => {
      console.log(error)
    });
  }

}
