import { Component, OnInit, ElementRef, AfterViewInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray, FormArrayName, FormControl } from '@angular/forms';
import { first } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Plate } from '../../../models/plate';
import { PlateService } from '../../../service/plate.service';

import { Location } from '../../../models/location';
import { LocationService } from '../../../service/location.service';

import { Article } from '../../../models/article'; 
import { ArticleService } from '../../../service/article.service';
import { ThrowStmt } from '@angular/compiler';

@Component({
  selector: 'app-plate-create',
  templateUrl: './plate-create.component.html',
  styleUrls: ['./plate-create.component.css']
})
export class PlateCreateComponent implements OnInit {
  
  @ViewChild('platedraw', { static: true })
  canvas: ElementRef<HTMLCanvasElement>;  
  
  private ctx: CanvasRenderingContext2D;
  errorMessage = '';
  plateForm;
  dims: FormArray;

  articles: Article[] = [];
  locations: Location[] = [];

  constructor(
    private formBuilder: FormBuilder, 
    private router: Router, 
    public locationService: LocationService, 
    public articleService: ArticleService, 
    public plateService: PlateService ) {
      this.plateForm = this.formBuilder.group({
        article_id: [''],
        name: ['', Validators.required],
        charge: ['', Validators.required],
        thickness: ['', Validators.required],
        pvc: [''],
        scotch: [''],
        location_id: ['', Validators.required],
        user_id: [''],
        dims: this.formBuilder.array([ this.createDim() ])
      });
   }

   createDim(): FormGroup{
     return this.formBuilder.group({
       x: [''],
       y: ['']
     })
   }

  addDim(): void {
    this.dims = this.plateForm.get('dims') as FormArray;
    this.dims.push(this.createDim());
   }

   removeDim(i): void {
    this.dims = this.plateForm.get('dims') as FormArray;
    this.dims.removeAt(i);
   }

   ngOnInit(): void {
    this.articleService.getArticles().subscribe((articles: Article[]) =>{
      let arts = articles['body'];
      
      for(let art of arts ){
        this.articles.push(new Article(
          art.article_id,
          art.name,
          art.description,
          art.date_created,
          art.uname,
          art.usurname,
          art.user_id,
          art.mname,
          art.fname
        ));
      }
    });

    this.locationService.getLocations().subscribe((locations: Location[]) => {
      let locs = locations['body'];
      
      for(let loc of locs ){
        this.locations.push(new Location(
          loc.location_id, 
          loc.name, 
          loc.date_created, 
          loc.uname, 
          loc.usurname, 
          loc.user_id
        ));
      }
    })

    this.ctx = this.canvas.nativeElement.getContext('2d');
    this.ctx.font = "16px Arial";
  }

  animate(): void {
    let dims =  this.plateForm.get('dims').controls;
    let x, y, x1, y1, distance, xm, ym;

    this.ctx.fillStyle = "#FFFFFF";
    this.ctx.fillRect(0,0,700,350);
    this.ctx.fillStyle = "black";

    this.ctx.beginPath();
    this.ctx.moveTo(5,5);

    xm = ym = 0;
    x1 = 5;
    y1 = 5;
    
    for( let el of dims ){
      x = (el.controls['x'].value/10)+5;
      y = (el.controls['y'].value/10)+5;
      this.ctx.lineTo(x,y);

      //distance between 2 points... formula src: https://www.mathplanet.com/education/algebra-2/conic-sections/distance-between-two-points-and-the-midpoint
      distance = Math.sqrt( (Math.pow((x1-x),2) + Math.pow((y1-y),2)) );

      //midpoint between 2 points... formula from same url as formula for distance
      xm = (x+x1)/2;
      ym = (y+y1)/2;

      //locate output text coordinates
      if( x1 == x ){
        this.ctx.fillText((distance*10).toString() + " mm", xm+20, ym );
      } else if( y1 == y ){
        this.ctx.fillText((distance*10).toString() + " mm", xm, ym+20 );
      } else{
        this.ctx.fillText((distance*10).toString() + " mm", xm+20, ym+20 );
      }

      x1 = x;
      y1 = y;
    }

    this.ctx.lineTo(5,5);

    distance = Math.sqrt( (Math.pow((x1-5),2) + Math.pow((y1-5),2)) );

    xm = (5+x1)/2;
    ym = (5+y1)/2;
    
    //locate output text coordinates
    if( x1 == x ){
      this.ctx.fillText((distance*10).toString() + " mm", xm+20, ym );
    } else if( y1 == y ){
      this.ctx.fillText((distance*10).toString() + " mm", xm, ym+20 );
    } 

    this.ctx.stroke();
    this.ctx.closePath();
  }

  createPlate(plateForm){
    this.plateForm.controls['user_id'].setValue(sessionStorage.getItem("user"));

    let formValid = true;

    if(this.plateForm.controls['article_id'].value == ''){
      //alert("Prosim vnesite ifro artikla!");
      this.errorMessage+="Vnesite ifro artikla!\n";
      formValid = false;
    }
    if(this.plateForm.controls['name'].value == ''){
      //alert("Prosim vnesite naziv ploe!");
      this.errorMessage+="Vnesite naziv ploe!\n";
      formValid = false;
    }
    if(this.plateForm.controls['charge'].value == '' ){
      //alert("Prosim vnesite aro!");
      this.errorMessage+="Vnesite aro!\n";
      formValid = false;
    }
    if(this.plateForm.controls['thickness'].value == '' || this.plateForm.controls['thickness'].value == null ){
      //alert("Prosim vnesite debelino ploe!");
      this.errorMessage+="Vnesite debelino ploe!\n";
      formValid = false;
    }
    if(this.plateForm.controls['location_id'].value == '' ){
      //alert("Prosim vnesite lokacijo ploe!");
      this.errorMessage+="Vnesite lokacijo ploe!\n";
      formValid = false;
    }
    if(((this.plateForm.controls['dims'].value[0].x == '' && this.plateForm.controls['dims'].value[0].y) == '') && this.plateForm.controls['dims'].value.length == 1){
      //alert("Prosim vnesite veljavne mere ploe!");
      this.errorMessage+="Vnesite veljavne mere ploe!";
      formValid = false;
    }

    if( formValid == true ){
      this.plateService.createPlate(this.plateForm.value).pipe(first()).subscribe(       
        data => {
          this.router.navigate(['/platelist']);
      },
      error => {
        console.log(error)
      });
    } else{
      alert(this.errorMessage)
    }
    this.errorMessage="";
    console.log(this.plateForm.controls['dims'].value.length);
  }

}
