import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './component/login/login.component';
import { RegisterComponent } from './component/register/register.component';
import { HomeComponent } from './component/home/home.component';
import { DashboardComponent } from './component/dashboard/dashboard.component';
import { MaterialListComponent } from './component/material/material-list/material-list.component';
import { FinishListComponent } from './component/finish/finish-list/finish-list.component';
import { LocationListComponent } from './component/location/location-list/location-list.component';
import { LocationCreateComponent } from './component/location/location-create/location-create.component';
import { ArticleListComponent } from './component/article/article-list/article-list.component';
import { ArticleCreateComponent } from './component/article/article-create/article-create.component';
import { PlateListComponent } from './component/plate/plate-list/plate-list.component';
import { PlateAllComponent } from './component/plate/plate-all/plate-all.component';
import { PlateCreateComponent } from './component/plate/plate-create/plate-create.component';
import { PlateUpdateComponent } from './component/plate/plate-update/plate-update.component';
import { PlatePrintComponent } from './component/plate/plate-print/plate-print.component';
import { PlateSplitComponent } from './component/plate/plate-split/plate-split.component';
import { PlateArchiveComponent } from './component/plate/plate-archive/plate-archive.component';
import { PlateprintDetailComponent } from './component/plate/plateprint-detail/plateprint-detail.component';
import { MaterialCreateComponent } from './component/material/material-create/material-create.component';
import { FinishCreateComponent } from './component/finish/finish-create/finish-create.component';

import { AuthGuard } from './service/auth.guard';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'home', component: HomeComponent },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent, canActivate: [AuthGuard] },
  { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
  { path: 'materiallist', component: MaterialListComponent, canActivate: [AuthGuard] },
  { path: 'materialcreate', component: MaterialCreateComponent, canActivate: [AuthGuard] },
  { path: 'finishlist', component: FinishListComponent, canActivate: [AuthGuard] },
  { path: 'finishcreate', component: FinishCreateComponent, canActivate: [AuthGuard] },
  { path: 'locationlist', component: LocationListComponent, canActivate: [AuthGuard] },
  { path: 'locationcreate', component: LocationCreateComponent, canActivate: [AuthGuard] },
  { path: 'articlelist', component: ArticleListComponent, canActivate: [AuthGuard] },
  { path: 'articlecreate', component: ArticleCreateComponent, canActivate: [AuthGuard] },
  { path: 'platelist', component: PlateListComponent, canActivate: [AuthGuard] },
  { path: 'platecreate', component: PlateCreateComponent, canActivate: [AuthGuard] },
  { path: 'plate/:plate_id', component: PlateUpdateComponent, canActivate: [AuthGuard] },
  { path: 'plateprint/:plate_id', component: PlatePrintComponent, canActivate: [AuthGuard] },
  { path: 'platearchive/:plate_id', component: PlateArchiveComponent, canActivate: [AuthGuard] },
  { path: 'plateprintdetail/:plateprint_id', component: PlateprintDetailComponent, canActivate: [AuthGuard] },
  { path: 'platesplit/:plate_id', component: PlateSplitComponent, canActivate: [AuthGuard] },
  { path: 'plates', component: PlateAllComponent, canActivate: [AuthGuard] },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

