export class PlateSplitComponent implements OnInit {
 
  @ViewChild('platedrawcurrent', { static: true }) canvas: ElementRef<HTMLCanvasElement>;  
  
  @ViewChild('platenew', { static: true }) canvasNew: ElementRef<HTMLCanvasElement>;
  @ViewChild('platesecond', { static: true }) canvasSecond: ElementRef<HTMLCanvasElement>;

  private ctx: CanvasRenderingContext2D;
  private ctxNew: CanvasRenderingContext2D;
  private ctxSecond: CanvasRenderingContext2D;

  id: number;
  dimsFirst: FormArray;
  dimsSecond: FormArray;
  articles: Article[] = [];
  locations: Location[] = [];
  plates: Plate[] = [];
  plate: Plate = new Plate();
  materials: Material[] = [];
  plateprints: Plateprint[] = [];
  plateForm;
  area: number;
  errorMessage = '';

  createDim(): FormGroup{
    return this.formBuilder.group({
      x: [''],
      y: ['']
    })
  }

  addDimFirst(): void {
   this.dimsFirst = this.plateForm.get('dimsFirst') as FormArray;
   this.dimsFirst.push(this.createDim());
  }

  addDimSecond(): void {
   this.dimsSecond = this.plateForm.get('dimsSecond') as FormArray;
   this.dimsSecond.push(this.createDim());
  }

  constructor(private formBuilder: FormBuilder, 
    private router: Router,
    private route: ActivatedRoute, 
    public locationService: LocationService, 
    public articleService: ArticleService, 
    public plateService: PlateService,
    public materialService: MaterialService ) {
  }

  ngOnInit(): void {
    
    this.getPlate();

    this.plateForm = this.formBuilder.group({
      plate_id: [''],
      location_idFirst: [''],
      location_idSecond: [''],
      dimsFirst: this.formBuilder.array([ this.createDim() ]),
      dimsSecond: this.formBuilder.array([ this.createDim() ]),
      name: [''],
      curr_name: [''],
      dimx: [''],
      dimy: [''],
      charge: [''],
      user_id: [],
      thickness: [],
      article_id: [],
      pvc: [],
      scotch: [],
      density: []
    });

    this.articleService.getArticles().subscribe((articles: Article[]) =>{
      let arts = articles['body'];
      
      for(let art of arts ){
        this.articles.push(new Article(
          art.article_id,
          art.name,
          art.description,
          art.date_created,
          art.uname,
          art.usurname,
          art.user_id,
          art.mname,
          art.fname
        ));
      }
    });
    
    this.locationService.getLocations().subscribe((locations: Location[]) => {
      let locs = locations['body'];
      
      for(let loc of locs ){
        this.locations.push(new Location(
          loc.location_id, 
          loc.name, 
          loc.date_created, 
          loc.uname, 
          loc.usurname, 
          loc.user_id
        ));
      }
    });

    this.materialService.getMaterials().subscribe((materials: Material[])=> {
      let mats = materials['body'];

      for( let mat of mats ){
        this.materials.push(new Material(mat.material_id, mat.name, mat.description, mat.date_created, mat.uname, mat.usurname, mat.density, mat.user_id));
      }
    });

    this.ctx = this.canvas.nativeElement.getContext('2d');
    this.ctxNew = this.canvasNew.nativeElement.getContext('2d');
    this.ctxSecond = this.canvasSecond.nativeElement.getContext('2d');
  }

  getPlate(): void {
    this.plateService.getPlate(+this.route.snapshot.paramMap.get('plate_id')).subscribe(data => {
      this.plate = data;
      this.canvasDraw();
    } );
    
  }

  canvasDraw(){
    let dimx = this.plate.dimx.split(",");
    let dimy = this.plate.dimy.split(",");
    
    let x, y, x1, y1, distance, xm, ym;

    this.ctx.fillStyle = "#FFFFFF";
    this.ctx.fillRect(0,0,700,350);
    this.ctx.fillStyle = "black";

    this.ctx.beginPath();
    this.ctx.moveTo(5,5);

    xm = ym = 0;
    x1 = 5;
    y1 = 5;

    for( let i = 0; i < dimx.length; i++ ){
      x = (parseInt(dimx[i])/10)+5;
      y = (parseInt(dimy[i])/10)+5;
      
      this.ctx.lineTo(x,y);

      //distance between 2 points... formula src: https://www.mathplanet.com/education/algebra-2/conic-sections/distance-between-two-points-and-the-midpoint
      distance = Math.sqrt( (Math.pow((x1-x),2) + Math.pow((y1-y),2)) );

      //midpoint between 2 points... formula from same url as formula for distance
      xm = (x+x1)/2;
      ym = (y+y1)/2;

      //locate output text coordinates
      if( x1 == 5 && x == 5 && y == 5 && y1 == 5 ){

      } else if( x1 == x ){
        this.ctx.fillText((distance*10).toString() + " mm", xm+20, ym );
      } else if( y1 == y ){
        this.ctx.fillText((distance*10).toString() + " mm", xm, ym+20 );
      } else{
        this.ctx.fillText((distance*10).toString() + " mm", xm+20, ym+20 );
      }

      x1 = x;
      y1 = y;
    }

    this.ctx.lineTo(5,5);
    this.ctx.stroke();
    this.ctx.closePath();
  }

  animateFirst(): void {
    let dims =  this.plateForm.get('dimsFirst').controls;
    let x, y, x1, y1, distance, xm, ym;

    this.ctxNew.fillStyle = "#FFFFFF";
    this.ctxNew.fillRect(0,0,700,350);
    this.ctxNew.fillStyle = "black";

    this.ctxNew.beginPath();
    this.ctxNew.moveTo(5,5);

    xm = ym = 0;
    x1 = 5;
    y1 = 5;

    for( let el of dims ){
      x = (el.controls['x'].value/10)+5;
      y = (el.controls['y'].value/10)+5;
      this.ctxNew.lineTo(x,y);

      //distance between 2 points... formula src: https://www.mathplanet.com/education/algebra-2/conic-sections/distance-between-two-points-and-the-midpoint
      distance = Math.sqrt( (Math.pow((x1-x),2) + Math.pow((y1-y),2)) );

      //midpoint between 2 points... formula from same url as formula for distance
      xm = (x+x1)/2;
      ym = (y+y1)/2;

      //locate output text coordinates
      if( x1 == x ){
        this.ctxNew.fillText((distance*10).toString() + " mm", xm+20, ym );
      } else if( y1 == y ){
        this.ctxNew.fillText((distance*10).toString() + " mm", xm, ym+20 );
      } else{
        this.ctxNew.fillText((distance*10).toString() + " mm", xm+20, ym+20 );
      }

      x1 = x;
      y1 = y;
    }
    this.ctxNew.lineTo(5,5);

    distance = Math.sqrt( (Math.pow((x1-5),2) + Math.pow((y1-5),2)) );

    xm = (5+x1)/2;
    ym = (5+y1)/2;
    
    //locate output text coordinates
    if( x1 == x ){
      this.ctxNew.fillText((distance*10).toString() + " mm", xm+20, ym );
    } else if( y1 == y ){
      this.ctxNew.fillText((distance*10).toString() + " mm", xm, ym+20 );
    } 

    this.ctxNew.stroke();
    this.ctxNew.closePath();
  }

  animateSecond(): void {
    let dims =  this.plateForm.get('dimsSecond').controls;
    let x, y, x1, y1, distance, xm, ym;

    this.ctxSecond.fillStyle = "#FFFFFF";
    this.ctxSecond.fillRect(0,0,700,350);
    this.ctxSecond.fillStyle = "black";

    this.ctxSecond.beginPath();
    this.ctxSecond.moveTo(5,5);

    xm = ym = 0;
    x1 = 5;
    y1 = 5;

    for( let el of dims ){
      x = (el.controls['x'].value/10)+5;
      y = (el.controls['y'].value/10)+5;
      this.ctxSecond.lineTo(x,y);

      //distance between 2 points... formula src: https://www.mathplanet.com/education/algebra-2/conic-sections/distance-between-two-points-and-the-midpoint
      distance = Math.sqrt( (Math.pow((x1-x),2) + Math.pow((y1-y),2)) );

      //midpoint between 2 points... formula from same url as formula for distance
      xm = (x+x1)/2;
      ym = (y+y1)/2;

      //locate output text coordinates
      if( x1 == x ){
        this.ctxSecond.fillText((distance*10).toString() + " mm", xm+20, ym );
      } else if( y1 == y ){
        this.ctxSecond.fillText((distance*10).toString() + " mm", xm, ym+20 );
      } else{
        this.ctxSecond.fillText((distance*10).toString() + " mm", xm+20, ym+20 );
      }

      x1 = x;
      y1 = y;
    }
    this.ctxSecond.lineTo(5,5);

    distance = Math.sqrt( (Math.pow((x1-5),2) + Math.pow((y1-5),2)) );

    xm = (5+x1)/2;
    ym = (5+y1)/2;
    
    //locate output text coordinates
    if( x1 == x ){
      this.ctxSecond.fillText((distance*10).toString() + " mm", xm+20, ym );
    } else if( y1 == y ){
      this.ctxSecond.fillText((distance*10).toString() + " mm", xm, ym+20 );
    } 

    this.ctxSecond.stroke();
    this.ctxSecond.closePath();
  }

  updatePlate(plateForm): void{
    this.plateForm.controls['plate_id'].setValue(+this.route.snapshot.paramMap.get('plate_id'));
    this.plateForm.controls['dimx'].setValue(this.plate.dimx);
    this.plateForm.controls['dimy'].setValue(this.plate.dimy);
    this.plateForm.controls['charge'].setValue(this.plate.charge);
    this.plateForm.controls['thickness'].setValue(this.plate.thickness);
    this.plateForm.controls['article_id'].setValue(this.plate.article_id);
    this.plateForm.controls['pvc'].setValue(this.plate.pvc);
    this.plateForm.controls['scotch'].setValue(this.plate.scotch);
    this.plateForm.controls['user_id'].setValue(parseInt(sessionStorage.getItem('user')));
    this.plateForm.controls['curr_name'].setValue(this.plate.name);
    
    let density = 0;
    let mat = this.materials.find(m => m.name == this.plate.material_name );
    density = mat.density;
    this.plateForm.controls['density'].setValue(density);
    console.log(this.plateForm.value);

    let currArea = this.plate.area;

    let area1 = 0;
    let area2 = 0;

    let polygon1 = this.plateForm.controls['dimsFirst'].value;
    let polygon2 = this.plateForm.controls['dimsSecond'].value;

    let formValid = true;

    //prepare array ( add { 'x': 0, 'y': 0} at start and end of polygon array)
    polygon1.unshift({ 'x': 0, 'y': 0});
    polygon1.push({ 'x': 0, 'y': 0});
    polygon2.unshift({ 'x': 0, 'y': 0});
    polygon2.push({ 'x': 0, 'y': 0});
    let n1 = polygon1.length;
    let n2 = polygon2.length;

    let j1 = n1 - 1;
    let j2 = n2 - 1;
    // javascript implementation of shoelace formula
    for( let i = 0; i < n1; i++ ){
      area1 += ( polygon1[j1].x + polygon1[i].x )*( polygon1[j1].y - polygon1[i].y );

      j1 = i;
    }
    for( let i = 0; i < n2; i++ ){
      area2 += ( polygon2[j2].x + polygon2[i].x )*( polygon2[j2].y - polygon2[i].y );

      j2 = i;
    }

    area1 = area1 / 2;
    area2 = area2 / 2;

    // convert area to square centimeters
    area1/=100;
    area2/=100;

    area1 = Math.abs(area1);
    area2 = Math.abs(area2);

    if(this.plateForm.controls['location_idFirst'].value == '' ){
      //alert("Prosim vnesite lokacijo ploe!");
      this.errorMessage+="Vnesite lokacijo prve ploe!\n";
      formValid = false;
    }
    if(this.plateForm.controls['location_idSecond'].value == '' ){
      //alert("Prosim vnesite lokacijo ploe!");
      this.errorMessage+="Vnesite lokacijo druge ploe!\n";
      formValid = false;
    }
    if(area1+area2 > currArea && area1 == 0 || area2 == 0 ){
      
      this.errorMessage+="Napaka pri vnosu plo!\n";
      formValid = false;
    } 
    if( formValid == false ){
      alert(this.errorMessage);
    }
    else{
      this.plateService.plateSplit(this.plateForm.value).pipe(first()).subscribe(       
        data => {
          console.log(data);
      },
      error => {
        console.log(error)
      });
    }

    this.errorMessage = "";

  }
}
