import { Component, OnInit, ElementRef, AfterViewInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray, FormArrayName, FormControl } from '@angular/forms';
import { first } from 'rxjs/operators';
import { Observable } from "rxjs";
import { HttpClient } from '@angular/common/http';

import { Plate } from '../../../models/plate';
import { PlateService } from '../../../service/plate.service';

import { Material } from '../../../models/material';
import { MaterialService } from '../../../service/material.service';

import { Finish } from '../../../models/finish';
import { FinishService } from '../../../service/finish.service';

@Component({
  selector: 'app-plate-list',
  templateUrl: './plate-list.component.html',
  styleUrls: ['./plate-list.component.css']
})
export class PlateListComponent implements OnInit {
  plates: Plate[] = [];
  res: Plate[] = [];
  finishes: Finish[] = [];
  weight: number = 0;
  materials: Material[] = [];

  searchForm;

  constructor( public plateService: PlateService, public finishService: FinishService, public materialService: MaterialService, private formBuilder: FormBuilder ) { 
    this.searchForm = this.formBuilder.group({
      area: [''],
      material_id: [''],
      finish_id: [''],
      pvc: [''],
      scotch: [''],
      thickness: ['']
    });
  }

  ngOnInit(): void {
    
    this.plateService.getPlates()
      .pipe(first())
      .subscribe( (plates: Plate[]) => {
        for( let plate of plates ){
          this.plates.push(plate);
        }
      }
    );

    this.res = this.plates;
    this.finishService.getFinishes().subscribe((finishes: Finish[]) => {
      let finishe = finishes['body'];

      for( let finish of finishe ){
        this.finishes.push(new Finish(finish.finish_id, finish.name, finish.description, finish.date_created, finish.uname, finish.usurname, finish.user_id));
      }
      //console.log( this.finishes );
    });

    this.materialService.getMaterials().subscribe((materials: Material[])=> {
      let mats = materials['body'];

      for( let mat of mats ){
        this.materials.push(new Material(mat.material_id, mat.name, mat.description, mat.date_created, mat.uname, mat.usurname, mat.density, mat.user_id));
      }
      //console.log(this.materials)
    });

    console.log(this.plates)
  }

  searchPlates(searchform: any): void {
  
    let scotch = 0;
    let pvc = 0;
    let area = 0;
    let thickness = 0;
    let material = "";
    let finish = "";
    let density = 0;

    if(this.searchForm.controls['area'].value != "" && this.searchForm.controls['area'].value != null){
      area = this.searchForm.controls['area'].value;
    }
    if(this.searchForm.controls['thickness'].value != "" && this.searchForm.controls['thickness'].value != null ){
      thickness = this.searchForm.controls['thickness'].value;
    }
    if(this.searchForm.controls['finish_id'].value != "" ){
      finish = this.searchForm.controls['finish_id'].value;
    }
    if(this.searchForm.controls['material_id'].value != "" ){
      material = this.searchForm.controls['material_id'].value;
    }
    if(this.searchForm.controls['pvc'].value == true ){
      pvc = 1;
    }
    if(this.searchForm.controls['scotch'].value == true ){
      scotch = 1;
    }

    this.plates = this.res;

    this.plates = this.plates.filter(p => p.area >= area);

    if( thickness != 0 ){
      this.plates = this.plates.filter(p => p.thickness == thickness );
    }
    if( material != "" ){
      this.plates = this.plates.filter(p => p.material_name == material );
      let mat = this.materials.find(m => m.name == material );
      density = mat.density;
    }
    if( finish != "" ){
      this.plates = this.plates.filter(p => p.finish_name == finish );
    }
    if( scotch == 1 ){
      this.plates = this.plates.filter(p => p.scotch == scotch );
    }
    if( pvc == 1 ){
      this.plates = this.plates.filter(p => p.pvc == pvc );
    }

    function compare(a: Plate, b: Plate ): number{
      if( a.area > b.area ) return 1;
      if( a.area < b.area ) return -1;
      return 0; 
    }

    this.plates.sort(compare);

    // area is in square cm, thickness is in mm
    let volume = area*(thickness/10);
    // density is g/cubic cm
    this.weight = (volume*density)/1000;

    console.log(this.plates);
  }


}