export class Article {
    public article_id: number;
    public name: string;
    public description: string;
    public date_created: string;
    public user_name: string;
    public user_surname: string;
    public user_id: number;
    public material_name: string;
    public finish_name: string;


    constructor( article_id: number, name: string, description: string, date_created: string, user_name: string, user_surname: string, user_id: number, material_name: string, finish_name: string ) {
        this.article_id = article_id;
        this.name = name;
        this.description = description;
        this.date_created = date_created;
        this.user_id = user_id;
        this.user_name = user_name;
        this.user_surname = user_surname;
        this.material_name = material_name;
        this.finish_name = finish_name;
    }
}