import { Component, OnInit } from '@angular/core';

import { Finish } from 'src/app/models/finish';
import { FinishService } from 'src/app/service/finish.service';

@Component({
  selector: 'app-finish-list',
  templateUrl: './finish-list.component.html',
  styleUrls: ['./finish-list.component.css']
})
export class FinishListComponent implements OnInit {
  
  finishes: Finish[];

  constructor( private finishService: FinishService ) { }

  ngOnInit(): void {
    this.finishService.getFinishes().subscribe((finishes: Finish[]) =>{
      this.finishes = finishes['body'];
      console.log(this.finishes[0]);
    })
  }
}
