import { Component, OnInit, ElementRef, AfterViewInit, ViewChild, AfterContentInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray, FormArrayName, FormControl } from '@angular/forms';
import { first, timeout } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';

import { Plate } from '../../../models/plate';
import { Plateprint } from '../../../models/plateprint';
import { PlateService } from '../../../service/plate.service';

import { Location } from '../../../models/location';
import { LocationService } from '../../../service/location.service';

import { Article } from '../../../models/article'; 
import { ArticleService } from '../../../service/article.service';

@Component({
  selector: 'app-plateprint-detail',
  templateUrl: './plateprint-detail.component.html',
  styleUrls: ['./plateprint-detail.component.css']
})
export class PlateprintDetailComponent implements OnInit {

  id: number;
  dims: FormArray;
  articles: Article[] = [];
  locations: Location[] = [];
  plates: Plate[] = [];
  plate: Plate = new Plate();
  plateprints: Plateprint[] = [];
  plateprint: any = 0;
  plateForm;
  area: number;
  
  constructor(private formBuilder: FormBuilder, 
    private router: Router,
    private route: ActivatedRoute, 
    public locationService: LocationService, 
    public articleService: ArticleService, 
    public plateService: PlateService) {
  }

  ngOnInit(): void {
  
    this.getPlateprint();

/*
    this.plateService.getPlateprints(+this.route.snapshot.paramMap.get('plate_id')).subscribe((plateprints: Plateprint[]) => {
      for( let arch of plateprints ){
        this.plateprints.push(new Plateprint(
          arch.plateprint_id,
          arch.plate_id,
          arch.date_created,
          arch.user_name,
          arch.user_surname,
          arch.user_id,
          arch.weight
        ))
      }
    });*/

    this.articleService.getArticles().subscribe((articles: Article[]) =>{
      let arts = articles['body'];
      
      for(let art of arts ){
        this.articles.push(new Article(
          art.article_id,
          art.name,
          art.description,
          art.date_created,
          art.uname,
          art.usurname,
          art.user_id,
          art.mname,
          art.fname
        ));
      }
    });
    
    this.locationService.getLocations().subscribe((locations: Location[]) => {
      let locs = locations['body'];
      
      for(let loc of locs ){
        this.locations.push(new Location(
          loc.location_id, 
          loc.name, 
          loc.date_created, 
          loc.uname, 
          loc.usurname, 
          loc.user_id
        ));
      }
    });

    
  }

  getPlate(id: number): void {
    this.plateService.getPlate(id).subscribe(data => {
      this.plate = data;
    } );
    
  }

  getPlateprint(): void{
    this.plateService.getPlateprint(+this.route.snapshot.paramMap.get('plateprint_id')).subscribe(data => {
      console.log(data);
      this.plateprint = new Plateprint(data.plateprint_id, data.plate_id, data.date_created, data.user_name, data.user_surname, data.user_id, data.weight);
      this.getPlate(data.plate_id);
      } 
    );
  }

  printfn(): void{
    console.log("print was clicked");
    let printcontent = document.getElementById('printarea').innerHTML;
    let originalpage = document.body.innerHTML;

    document.body.innerHTML = printcontent;

    window.print();

    document.body.innerHTML = originalpage;
    
  }

}
