import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Location } from '../../../models/location';
import { LocationService } from '../../../service/location.service';

@Component({
  selector: 'app-location-create',
  templateUrl: './location-create.component.html',
  styleUrls: ['./location-create.component.css']
})
export class LocationCreateComponent implements OnInit {

  locationForm;

  constructor( public locationService: LocationService, private formBuilder: FormBuilder, private router: Router ) {
    this.locationForm = this.formBuilder.group({
      name: ['', Validators.required],
      user_id: ['']
    });
  }

  ngOnInit(): void {
  }

  createLocation(locationForm){
    this.locationForm.controls['user_id'].setValue(sessionStorage.getItem("user"));
    this.locationService.createLocation(this.locationForm.value.name, this.locationForm.value.user_id).pipe(first()).subscribe(       
      data => {
      this.router.navigate(['/locationlist']);
    },
    error => {
      console.log(error)
    });
    console.log(this.locationForm.value);
  }
}
