export class Archive {
    public platearchive_id: number;
    public plate_id: number;
    public date_created: string;
    public user_name: string;
    public user_surname: string;
    public user_id: number;
    public thickness: number;
    public area: number;
    public dimx: string;
    public dimy: string;
    public density: number;
    public weight: number;
    
    constructor( platearchive_id: number, plate_id: number, date_created: string, user_name: string, user_surname: string, user_id: number, thickness: number, area: number, dimx: string, dimy: string, density: number, weight: number ) {
        this.platearchive_id = platearchive_id;
        this.plate_id = plate_id;
        this.date_created = date_created;
        this.user_id = user_id;
        this.user_name = user_name;
        this.user_surname = user_surname;
        this.thickness = thickness;
        this.dimx = dimx;
        this.dimy = dimy;
        this.area = area;
        this.density = density;
        this.weight = weight;
    }
    
}