import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Finish } from 'src/app/models/finish';
import { FinishService } from 'src/app/service/finish.service';

@Component({
  selector: 'app-finish-create',
  templateUrl: './finish-create.component.html',
  styleUrls: ['./finish-create.component.css']
})
export class FinishCreateComponent implements OnInit {

  finishForm;

  constructor(public finishService: FinishService, private formBuilder: FormBuilder, private router: Router) { 
    this.finishForm = this.formBuilder.group({
      name: ['', Validators.required],
      user_id: ['']
    });
  }

  ngOnInit(): void {
  }

  createFinish( finishForm ): void {
    this.finishForm.controls['user_id'].setValue(sessionStorage.getItem("user"));
    
    this.finishService.createFinish(this.finishForm.value).pipe(first()).subscribe(       
      data => {
      this.router.navigate(['/finishlist']);
    },
    error => {
      console.log(error)
    });
  }

}
