import { Component, OnInit, ElementRef, AfterViewInit, ViewChild, AfterContentInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray, FormArrayName, FormControl } from '@angular/forms';
import { first, timeout } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';

import { Plate } from '../../../models/plate';
import { Archive } from '../../../models/archive';
import { PlateService } from '../../../service/plate.service';

import { Location } from '../../../models/location';
import { LocationService } from '../../../service/location.service';

import { Article } from '../../../models/article'; 
import { ArticleService } from '../../../service/article.service';

@Component({
  selector: 'app-plate-archive',
  templateUrl: './plate-archive.component.html',
  styleUrls: ['./plate-archive.component.css']
})
export class PlateArchiveComponent implements OnInit {
  
  id: number;
  dims: FormArray;
  articles: Article[] = [];
  locations: Location[] = [];
  plates: Plate[] = [];
  plate: Plate = new Plate();
  archives: Archive[] = [];
  plateForm;
  area: number;
  
  constructor(private formBuilder: FormBuilder, 
    private router: Router,
    private route: ActivatedRoute, 
    public locationService: LocationService, 
    public articleService: ArticleService, 
    public plateService: PlateService) {
  }

  ngOnInit(): void {
    
    this.getPlate();

    this.plateService.getArchives(+this.route.snapshot.paramMap.get('plate_id')).subscribe((archive: Archive[]) => {
      for( let arch of archive ){
        this.archives.push(new Archive(
          arch.platearchive_id,
          arch.plate_id,
          arch.date_created,
          arch.user_name,
          arch.user_surname,
          arch.user_id,
          arch.thickness,
          arch.area,
          arch.dimx,
          arch.dimy,
          arch.density,
          arch.weight
        ))
      }
    });

    console.log(this.archives);

    this.articleService.getArticles().subscribe((articles: Article[]) =>{
      let arts = articles['body'];
      
      for(let art of arts ){
        this.articles.push(new Article(
          art.article_id,
          art.name,
          art.description,
          art.date_created,
          art.uname,
          art.usurname,
          art.user_id,
          art.mname,
          art.fname
        ));
      }
    });
    
    this.locationService.getLocations().subscribe((locations: Location[]) => {
      let locs = locations['body'];
      
      for(let loc of locs ){
        this.locations.push(new Location(
          loc.location_id, 
          loc.name, 
          loc.date_created, 
          loc.uname, 
          loc.usurname, 
          loc.user_id
        ));
      }
    });
  }

  getPlate(): void {
    this.plateService.getPlate(+this.route.snapshot.paramMap.get('plate_id')).subscribe(data => {
      this.plate = data;
    } );
    
  }
}
