import { Component, OnInit, ElementRef, AfterViewInit, ViewChild, AfterContentInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray, FormArrayName, FormControl } from '@angular/forms';
import { first, timeout } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';

import { Plate } from '../../../models/plate';
import { PlateService } from '../../../service/plate.service';

import { Location } from '../../../models/location';
import { LocationService } from '../../../service/location.service';

import { Article } from '../../../models/article'; 
import { ArticleService } from '../../../service/article.service';
import { ThrowStmt } from '@angular/compiler';
import { getInterpolationArgsLength } from '@angular/compiler/src/render3/view/util';

import { Material } from '../../../models/material';
import { MaterialService } from '../../../service/material.service';

@Component({
  selector: 'app-plate-update',
  templateUrl: './plate-update.component.html',
  styleUrls: ['./plate-update.component.css']
})
export class PlateUpdateComponent implements OnInit {

  @ViewChild('platedraw', { static: true }) canvas: ElementRef<HTMLCanvasElement>;  
  
  @ViewChild('platenew', { static: true }) canvasNew: ElementRef<HTMLCanvasElement>;  

  private ctx: CanvasRenderingContext2D;
  private ctxNew: CanvasRenderingContext2D;

  id: number;
  dims: FormArray;
  articles: Article[] = [];
  locations: Location[] = [];
  plates: Plate[] = [];
  plate: Plate = new Plate();
  plateForm;
  area: number;
  materials: Material[] = [];

  constructor(private formBuilder: FormBuilder, 
    private router: Router,
    private route: ActivatedRoute, 
    public locationService: LocationService, 
    public articleService: ArticleService, 
    public plateService: PlateService,
    public materialService: MaterialService) {
  }


  createDim(): FormGroup{
    return this.formBuilder.group({
      x: [''],
      y: ['']
    })
  }

  addDim(): void {
   this.dims = this.plateForm.get('dims') as FormArray;
   this.dims.push(this.createDim());
  }

  getPlate(): void {
    this.plateService.getPlate(+this.route.snapshot.paramMap.get('plate_id')).subscribe(data => {
      this.plate = data;
      this.canvasDraw();
    } );
    
  }

  ngOnInit(): void {

    this.getPlate();

    this.plateForm = this.formBuilder.group({
      plate_id: [''],
      active: [''],
      location_id: [''],
      dims: this.formBuilder.array([ this.createDim() ]),
      dimx: [''],
      dimy: [''],
      loc: [''],
      act: [''],
      charge: [''],
      chrg: [''],
      user_id: [],
      thickness: [],
      density: []
    });

    this.articleService.getArticles().subscribe((articles: Article[]) =>{
      let arts = articles['body'];
      
      for(let art of arts ){
        this.articles.push(new Article(
          art.article_id,
          art.name,
          art.description,
          art.date_created,
          art.uname,
          art.usurname,
          art.user_id,
          art.mname,
          art.fname
        ));
      }
    });
    
    this.locationService.getLocations().subscribe((locations: Location[]) => {
      let locs = locations['body'];
      
      for(let loc of locs ){
        this.locations.push(new Location(
          loc.location_id, 
          loc.name, 
          loc.date_created, 
          loc.uname, 
          loc.usurname, 
          loc.user_id
        ));
      }
    });

    this.materialService.getMaterials().subscribe((materials: Material[])=> {
      let mats = materials['body'];

      for( let mat of mats ){
        this.materials.push(new Material(mat.material_id, mat.name, mat.description, mat.date_created, mat.uname, mat.usurname, mat.density, mat.user_id));
      }
      //console.log(this.materials)
    });

    this.ctx = this.canvas.nativeElement.getContext('2d');
    this.ctxNew = this.canvasNew.nativeElement.getContext('2d');
    
    this.ctx.font = "16px Arial";
    this.ctxNew.font = "16px Arial";
  }

  canvasDraw(){
    let dimx = this.plate.dimx.split(",");
    let dimy = this.plate.dimy.split(",");
    
    let x, y, x1, y1, distance, xm, ym;

    this.ctx.fillStyle = "#FFFFFF";
    this.ctx.fillRect(0,0,700,350);
    this.ctx.fillStyle = "black";

    this.ctx.beginPath();
    this.ctx.moveTo(5,5);

    xm = ym = 0;
    x1 = 5;
    y1 = 5;

    for( let i = 0; i < dimx.length; i++ ){
      x = (parseInt(dimx[i])/10)+5;
      y = (parseInt(dimy[i])/10)+5;
      
      this.ctx.lineTo(x,y);

      //distance between 2 points... formula src: https://www.mathplanet.com/education/algebra-2/conic-sections/distance-between-two-points-and-the-midpoint
      distance = Math.sqrt( (Math.pow((x1-x),2) + Math.pow((y1-y),2)) );

      //midpoint between 2 points... formula from same url as formula for distance
      xm = (x+x1)/2;
      ym = (y+y1)/2;

      //locate output text coordinates
      if( x1 == 5 && x == 5 && y == 5 && y1 == 5 ){

      } else if( x1 == x ){
        this.ctx.fillText((distance*10).toString() + " mm", xm+20, ym );
      } else if( y1 == y ){
        this.ctx.fillText((distance*10).toString() + " mm", xm, ym+20 );
      } else{
        this.ctx.fillText((distance*10).toString() + " mm", xm+20, ym+20 );
      }

      x1 = x;
      y1 = y;
    }

    this.ctx.lineTo(5,5);
    this.ctx.stroke();
    this.ctx.closePath();
  }

  animate(): void {
    let dims =  this.plateForm.get('dims').controls
    let x, y, x1, y1, distance, xm, ym;

    this.ctxNew.fillStyle = "#FFFFFF";
    this.ctxNew.fillRect(0,0,700,350);
    this.ctxNew.fillStyle = "black";

    this.ctxNew.beginPath();
    this.ctxNew.moveTo(5,5);

    xm = ym = 0;
    x1 = 5;
    y1 = 5;

    for( let el of dims ){
      x = (el.controls['x'].value/10)+5;
      y = (el.controls['y'].value/10)+5;
      this.ctxNew.lineTo(x,y);

      //distance between 2 points... formula src: https://www.mathplanet.com/education/algebra-2/conic-sections/distance-between-two-points-and-the-midpoint
      distance = Math.sqrt( (Math.pow((x1-x),2) + Math.pow((y1-y),2)) );

      //midpoint between 2 points... formula from same url as formula for distance
      xm = (x+x1)/2;
      ym = (y+y1)/2;

      //locate output text coordinates
      if( x1 == x ){
        this.ctxNew.fillText((distance*10).toString() + " mm", xm+20, ym );
      } else if( y1 == y ){
        this.ctxNew.fillText((distance*10).toString() + " mm", xm, ym+20 );
      } else{
        this.ctxNew.fillText((distance*10).toString() + " mm", xm+20, ym+20 );
      }

      x1 = x;
      y1 = y;
    }
    this.ctxNew.lineTo(5,5);

    distance = Math.sqrt( (Math.pow((x1-5),2) + Math.pow((y1-5),2)) );

    xm = (5+x1)/2;
    ym = (5+y1)/2;
    
    //locate output text coordinates
    if( x1 == x ){
      this.ctxNew.fillText((distance*10).toString() + " mm", xm+20, ym );
    } else if( y1 == y ){
      this.ctxNew.fillText((distance*10).toString() + " mm", xm, ym+20 );
    } 

    this.ctxNew.stroke();
    this.ctxNew.closePath();
  }

  updatePlate(plateForm){
    this.plateForm.controls['plate_id'].setValue(+this.route.snapshot.paramMap.get('plate_id'));
    this.plateForm.controls['dimx'].setValue(this.plate.dimx);
    this.plateForm.controls['dimy'].setValue(this.plate.dimy);
    this.plateForm.controls['loc'].setValue(this.plate.location_id);
    this.plateForm.controls['act'].setValue(this.plate.active);
    this.plateForm.controls['chrg'].setValue(this.plate.charge);
    this.plateForm.controls['thickness'].setValue(this.plate.thickness);
    this.plateForm.controls['user_id'].setValue(parseInt(sessionStorage.getItem('user')));


    let density = 0;
    let mat = this.materials.find(m => m.name == this.plate.material_name );
    density = mat.density;
    this.plateForm.controls['density'].setValue(density);
    console.log(this.plateForm.value);

    let area = 0;

    let polygon = this.plateForm.controls['dims'].value;

    //console.log(this.plateForm.controls['dims'].value);
    //prepare array ( add { 'x': 0, 'y': 0} at start and end of polygon array)
    polygon.unshift({ 'x': 0, 'y': 0});
    polygon.push({ 'x': 0, 'y': 0});
    let n = polygon.length;
    let j = n - 1;
    //console.log(this.plateForm.controls['dims'].value);
    // javascript implementation of shoelace formula
    for( let i = 0; i < n; i++ ){
      area += ( polygon[j].x + polygon[i].x )*( polygon[j].y - polygon[i].y );

      j = i;
    }

    area = area / 2;

    // convert area to square centimeters
    area/=100;

    let currArea = this.plate.area;

    area = Math.abs(area);

    if(area > currArea ){
      alert("Ploa ki ste jo vnesli je veja od trenutno vnesene ploe! Prosim popravite vnos!");
    }
    else{
      console.log("Posodobi ploo!");
      this.plateService.updatePlate(this.plateForm.value).pipe(first()).subscribe(       
        data => {
          location.reload();
          //this.router.navigate(['/plate', +this.route.snapshot.paramMap.get('plate_id')]);
          //console.log(data);
      },
      error => {
        console.log(error);
      });
    }
  }
}
