import { Injectable } from '@angular/core';
import { map, catchError } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { Plate } from '../models/plate';
import { Archive } from '../models/archive';
import { Router } from '@angular/router';
import { Plateprint } from '../models/plateprint';

@Injectable({
  providedIn: 'root'
})
export class PlateService {
  //apiUrl: string = "http://localhost/plateapp/php";
  apiUrl: string = "192.168.1.105/php";

  constructor(    
    private httpClient: HttpClient,
    private router: Router ) {
  }

  public getPlates(): Observable<Plate[]>{
    return this.httpClient.get<Plate[]>(this.apiUrl + '/api/plate/read.php');
  }

  public createPlate(data: JSON): Observable<any>{
    return this.httpClient.post<any>(this.apiUrl + '/api/plate/create.php', data)
    .pipe(map(res => {
      console.log(JSON.stringify(res));
      return res;
    }),
    catchError(this.handleError));
  }

  public getPlate(id: number ): Observable<Plate>{
    return this.httpClient.get<Plate>(`${this.apiUrl}/api/plate/readOne.php?id=${id}` );
  }

  public getArchives( id: number ): Observable<Archive[]>{
    return this.httpClient.get<any>(`${this.apiUrl}/api/archive/read.php?id=${id}`);
  }

  public getPlateprints( id: number ): Observable<Plateprint[]>{
    return this.httpClient.get<any>(`${this.apiUrl}/api/plateprint/read.php?id=${id}`);
  }

  public getPlateprint(id: number ): Observable<Plateprint>{
    return this.httpClient.get<Plateprint>(`${this.apiUrl}/api/plateprint/readOne.php?id=${id}`);
  }

  public updatePlate( data: any ): Observable<any>{
    //console.log(JSON.stringify(data));
    return this.httpClient.post<any>(this.apiUrl + '/api/plate/update.php', data).pipe(map(res => {
      console.log(JSON.stringify(res));
      return res;
    }),
    catchError(this.handleError));
  }

  handleError(error: HttpErrorResponse) {
    let msg = '';
    if (error.error instanceof ErrorEvent) {
      // client-side error
      msg = error.error.message;
    } else {
      // server-side error
      msg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(msg);
  }

  public plateSplit( data: any ): Observable<any>{
    //console.log(JSON.stringify(data));
    return this.httpClient.post<any>(this.apiUrl + '/api/plate/platesplit.php', data).pipe(map(res => {
      console.log(JSON.stringify(res));
      return res;
    }),
    catchError(this.handleError));
  }
}
