import { Injectable } from '@angular/core';
import { map, catchError } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { Finish } from '../models/finish';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class FinishService {
  //apiUrl: string = "http://localhost/plateapp/php";
  apiUrl: string = "192.168.1.105/php";

  constructor(    
    private httpClient: HttpClient,
    private router: Router ) {
  }

  public getFinishes(): Observable<Finish[]>{
    return this.httpClient.get<Finish[]>(this.apiUrl + '/api/finish/read.php');
  }

  public createFinish(data): Observable<any>{
    return this.httpClient.post<any>(this.apiUrl + '/api/finish/create.php', data)
    .pipe(map(res => {
      console.log(res);
      return res;
    }),
    catchError(this.handleError));
  }

  handleError(error: HttpErrorResponse) {
    let msg = '';
    if (error.error instanceof ErrorEvent) {
      // client-side error
      msg = error.error.message;
    } else {
      // server-side error
      msg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(msg);
  }
}
