import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Material } from '../../../models/material';
import { MaterialService } from '../../../service/material.service';

import { Finish } from '../../../models/finish';
import { FinishService } from '../../../service/finish.service';

import { Article } from '../../../models/article';
import { ArticleService } from '../../../service/article.service';

@Component({
  selector: 'app-article-create',
  templateUrl: './article-create.component.html',
  styleUrls: ['./article-create.component.css']
})
export class ArticleCreateComponent implements OnInit {

  articleForm;

  article: Article;

  finishes: Finish[] = [];
  materials: Material[] = [];

  constructor(private formBuilder: FormBuilder, private router: Router, public materialService: MaterialService, public finishService: FinishService, public articleService: ArticleService) { 
    this.articleForm = this.formBuilder.group({
      name: ['', Validators.required],
      description: [''],
      material_id: [''],
      finish_id: [''],
      user_id: ['']
    })
  }

  ngOnInit(): void {
    this.finishService.getFinishes().subscribe((finishes: Finish[]) => {
      let finishe = finishes['body'];

      for( let finish of finishe ){
        this.finishes.push(new Finish(finish.finish_id, finish.name, finish.description, finish.date_created, finish.uname, finish.usurname, finish.user_id));
      }
      //console.log( this.finishes );
    });
    this.materialService.getMaterials().subscribe((materials: Material[])=> {
      let mats = materials['body'];

      for( let mat of mats ){
        this.materials.push(new Material(mat.material_id, mat.name, mat.description, mat.date_created, mat.uname, mat.usurname, mat.density, mat.user_id));
      }
      //console.log(this.materials)
    });
  }


  createArticle(articleForm){
    this.articleForm.controls['user_id'].setValue(sessionStorage.getItem("user"));

    this.articleService.createArticle(this.articleForm.value).pipe(first()).subscribe(       
      data => {
        this.router.navigate(['/articlelist']);
    },
    error => {
      console.log(error)
    });
    console.log(this.articleForm.value);
  }
}
