import { Injectable } from '@angular/core';
import { map, catchError } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { User } from '../models/user';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  //apiUrl: string = "http://localhost/plateapp/php";
  apiUrl: string = "192.168.1.105/php";

  constructor(
    private httpClient: HttpClient,
    private router: Router
  ) { }

  public userLogin(email: string, password: string ){
    return this.httpClient.post<any>(this.apiUrl + '/login.php', { email, password })
          .pipe(map(res => {
            sessionStorage.setItem('access_token', res.token);
            sessionStorage.setItem('user', res.user );
            return res;
          }),
          catchError(this.handleError));
  }

  signUp(user: User): Observable<any> {
    return this.httpClient.post(this.apiUrl + '/register.php', user)
    .pipe(map(res => {
      console.log(res);
      return res;
    }),
    catchError(this.handleError));
  }

  getToken(){
    return sessionStorage.getItem('access_token');
  }

  handleError(error: HttpErrorResponse) {
    let msg = '';
    if (error.error instanceof ErrorEvent) {
      // client-side error
      msg = error.error.message;
    } else {
      // server-side error
      msg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(msg);
  }

  get isLoggedIn(): boolean {
    let authToken = sessionStorage.getItem('access_token');
    return (authToken !== null) ? true : false;
  }

  doLogout() {
    let removeToken = sessionStorage.removeItem('access_token');
    if (removeToken == null) {
      this.router.navigate(['home']);
    }
  }

}
