export class Plateprint {
    public plateprint_id: number;
    public plate_id: number;
    public date_created: string;
    public user_name: string;
    public user_surname: string;
    public user_id: number;
    public weight: number;
    
    constructor( plateprint_id: number, plate_id: number, date_created: string, user_name: string, user_surname: string, user_id: number, weight: number ) {
        this.plateprint_id = plateprint_id;
        this.plate_id = plate_id;
        this.date_created = date_created;
        this.user_id = user_id;
        this.user_name = user_name;
        this.user_surname = user_surname;
        this.weight = weight;
    }
    
}